<template>

  <div id="app" v-on:scroll.passive="showHeader">

    <header id="page__header__container">
      <div id="page__header__wrapper">
        <!--<transition name="logo" appear>-->
        <router-link to="/" id="header_logo">
          <div></div>
          <img src="./assets/images/L144.png" alt="логотип" v-if="isLogoBlack" v-on:click="clickMenuOff">
          <img src="./assets/images/L144-w.png" alt="логотип" v-if="!isLogoBlack" v-on:click="clickMenuOff">
          <div></div>
        </router-link>
        <!--</transition>-->
        <div></div>
        <nav id="header_navigation" v-on:click="clickMenuOn">
          <router-link to="/" id="header_navigation_aboutschool">
            О школе
          </router-link>
          <router-link to="/menu" id="header_navigation_menu">
            Меню
          </router-link>
        </nav>
      </div>
    </header>

    <main id="page__content">
      <router-view></router-view>
    </main>

    <Menu/>

    <footer id="page__footer">
      2021
    </footer>

  </div>

</template>

<script>

export default {

  data() {
    return {
      isVisibleHeader: true,
      isLogoBlack: false
    }},
  methods: {
    showHeader: function () {
      let elem = document.getElementById('app');
      let elemHeader = document.getElementById('page__header__container');
      let position = elem.getBoundingClientRect();
      let y = position.top;
      let windowInnerHeight = document.documentElement.clientHeight;
      if (y < -windowInnerHeight + 95 && this.isVisibleHeader){
        elemHeader.style.backgroundColor = 'rgba(100, 100,100, .4)';
        elemHeader.style.backdropFilter = "blur(20px)";
        elemHeader.style.webkitBackdropFilter = "blur(20px)";
        let elemHeaderNavigation = document.getElementById('header_navigation_aboutschool');
        elemHeaderNavigation.style.color = "#fff";
        let elemHeaderNavigationMenu = document.getElementById('header_navigation_menu');
        elemHeaderNavigationMenu.style.color = "#fff";
        this.isLogoBlack = false
      }
      if ( y >= -windowInnerHeight + 95 && this.isVisibleHeader) {
        elemHeader.style.backgroundColor = 'rgba(100, 100,100, 0)';
        elemHeader.style.backdropFilter = "blur(0px)";
        elemHeader.style.webkitBackdropFilter = "blur(0px)";
        let elemHeaderNavigation = document.getElementById('header_navigation_aboutschool');
        elemHeaderNavigation.style.color = "#555";
        let elemHeaderNavigationMenu = document.getElementById('header_navigation_menu');
        elemHeaderNavigationMenu.style.color = "#555";
        this.isLogoBlack = true
      }
    },
    clickMenuOff: function () {
      let elemHeader = document.getElementById('page__header__container');
      elemHeader.style.backgroundColor = 'rgba(100, 100,100, 0)';
      elemHeader.style.backdropFilter = "blur(0px)";
      elemHeader.style.webkitBackdropFilter = "blur(0px)";
      let elemHeaderNavigation = document.getElementById('header_navigation_aboutschool');
      elemHeaderNavigation.style.color = "#555";
      let elemHeaderNavigationMenu = document.getElementById('header_navigation_menu');
      elemHeaderNavigationMenu.style.color = "#555";
      this.isVisibleHeader = true
      this.isLogoBlack = true
    },
    clickMenuOn: function () {
      let elemHeader = document.getElementById('page__header__container');
      elemHeader.style.backgroundColor = 'rgba(100, 100,100, .4)';
      elemHeader.style.backdropFilter = "blur(20px)";
      elemHeader.style.webkitBackdropFilter = "blur(20px)";
      let elemHeaderNavigation = document.getElementById('header_navigation_aboutschool');
      elemHeaderNavigation.style.color = "#fff";
      let elemHeaderNavigationMenu = document.getElementById('header_navigation_menu');
      elemHeaderNavigationMenu.style.color = "#fff";
      this.isVisibleHeader = false
      this.isLogoBlack = false
    }
  },
  mounted() {
    window.addEventListener("scroll", this.showHeader);
    this.showHeader()
  }
}
</script>

<style>

