  --media-min-width-middle: 980px;        /*для media*/
  --media-min-width-large:  1200px;       /*для media*/
  --width-wrapper-small:    95vw;
  --width-wrapper-middle:   90vw;
  --width-wrapper-large:    1024px;

  --heght-page:           calc(100vh - 75px);;
