@media (max-width: 800px) {
  #header_navigation {
    grid-template-columns:  auto;
  }
  #header_navigation_aboutschool {
    display: none;
  }
}

#page__footer {
  display: 			    grid;
  background-color:       rgba( 0, 0, 0,.8);
  align-items:            center;
  justify-items:          center;
