  max-height:             2rem;
}

.logo-enter-active {
  animation: logo-in 3s;
}

@keyframes logo-in {
  from {
    transform:  rotate3d(0, 1, 0, -360deg);
    animation-timing-function: linear;
  }

  50% {
    transform:  rotate3d(0, 1, 0, -360deg);
    animation-timing-function: linear;
  }

  74% {
    transform:  rotate3d(0, 1, 0, -180deg);
    animation-timing-function: linear;
  }

  to {
