<template>

  <transition name="slideViewEnter" appear>
  <div class="home">

    <div id="page_container">

      <section id="section_fixed_1"></section>

      <section class="section" id="section_1" v-on:scroll.passive="showSection_1">
        <transition name="slide-fade-left">
          <div class="section_wrapper" id="section_wrapper_1"  v-if="isShowSection_1">
            <!-- isShowSection_1 по умолчанию false. При прокрутке в методе showSection_1
            isShowSection_1 становится true и элемент появляется -->
            <picture id="section_1_picture">
              <source srcset="../assets/images/main-background.png" media="(min-width: 1200px)" />
              <source srcset="../assets/images/main-background.png" media="(min-width: 800px)" />
              <img src="../assets/images/main-background.png" alt="velo" />
            </picture>
            <div id="section_1_text">
              <p class="section_1_text_p">Лицей с углубленным изучением всего</p>
              <p class="section_1_text_p">Санкт-Петербург</p>
            </div>
          </div>
        </transition>
      </section>

      <section id="section_fixed_2"></section>

      <section class="section" id="section_2" v-on:scroll.passive="showSection_2">
        <transition name="slide-fade">
          <div class="section_wrapper" id="section_wrapper_2" v-if="isShowSection_2">
            <!-- isShowSection_2 по умолчанию false. При прокрутке в методе showSection_2
            isShowSection_2 становится true и элемент появляется -->
            <img src="../assets/images/mathematics-989121_1920.png" alt="pi">
            <div id="section_2_text">
              <p class="section_2_text_p">Инженерное направление</p>
            </div>
          </div>
        </transition>
      </section>

    </div>

  </div>
  </transition>
</template>

<script>
// @ is an alias to /src
//import HomeComp from '../components/HomeComp.vue'

export default {
  name: 'Home',
  data() {
    return {
      isShowSection_1: false,
      isShowSection_2: false,
    }},
  methods: {
    showSection_1: function () {
      let elem = document.getElementById('section_1');
      let position = elem.getBoundingClientRect();
      let y = position.top;
      let windowInnerHeight = document.documentElement.clientHeight;
      if(y < windowInnerHeight/3) {
        this.isShowSection_1 = true
      }
    },
    showSection_2: function () {
      let elem = document.getElementById('section_2');
      let position = elem.getBoundingClientRect();
      let y = position.top;
      let windowInnerHeight = document.documentElement.clientHeight;
      if (y < windowInnerHeight / 3) {
        this.isShowSection_2 = true
      }
    }
  },
  mounted: function(){
    window.addEventListener("scroll", this.showSection_1);
    window.addEventListener("scroll", this.showSection_2);
  },
  beforeRouteLeave: function(){
    window.removeEventListener("scroll", this.showSection_1);
    window.removeEventListener("scroll", this.showSection_2);
  }
}

</script>

